import ContentArea from '@/views/layout/ContentArea.vue';

export const appRouter = [
    {
        path: '/',
        title: '',
        component: ContentArea,
        redirect: '/dashboard',
        children: [
            {
                path: 'dashboard',
                meta: {
                    hidden: true, // hidden 
                    breadMenu: 'dashboard'
                },
                component: () => import('@/views/dashboard/index'),
                name: 'dashboard',
                icon: 'el-icon-s-home'
            }
        ]
    },
    {
        path: '/networkManage',
        name: 'networkManage',
        title: '',
        icon: 'el-icon-s-marketing',
        redirect: '/topology',
        component: ContentArea,
        children: [
            {
                path: '/topology',
                name: 'topology',
                title: '',
                component: resolve => {
                    require(['@/views/networkManage/topology/index.vue'], resolve);
                }
            }
        ]
    }
];

// routers
export default appRouter;
