const globalData = {
    // 
    PANELTYPES: {
        DEVICE: '',
        MCB: ''
    },
    // 
    MAJORTYPES: {
        1: '',
        2: ''
    },
    // 
    SENSORTYPES: {
        0: '',
        1: ''
    },
    // 
    ACCESSTYPES: {
        1: '4G',
        2: ''
    },
    globalFun: () => {
        alert('');
    }
};
const global = {
    install: function(Vue, options) {
        for (let g of Object.keys(globalData)) {
            Vue.prototype[g] = globalData[g];
        }
    }
};
export default global;
