import Vue from 'vue';
import confirm from './confirm';

// VueVue.extend,vue-loader.default
const ConfirmConstructor = Vue.extend(confirm);

let nId = 1;

export default options => {
    let id = 'confirm-' + nId++;
    options = options || {};

    const ConfirmInstance = new ConfirmConstructor({
        data: options
    });
    ConfirmInstance.id = id;
    ConfirmInstance.vm = ConfirmInstance.$mount(); // domVue
    ConfirmInstance.vm.visible = true;
    ConfirmInstance.dom = ConfirmInstance.vm.$el; // dom
    document.body.appendChild(ConfirmInstance.dom); // dombody
    ConfirmInstance.dom.style.zIndex = nId + 1001; // z-index1
    return ConfirmInstance.vm;
};
