<style lang='less' module>
.UploadParseExcel {
    display: inline-block;
    h4 {
        color: @color-warning;
    }
}
</style>
<template>
  <div :class="$style.UploadParseExcel">
    <el-upload
      action=""
      :multiple="false"
      :show-file-list="false"
      accept="csv, application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      :http-request="httpRequest"
    >
      <slot></slot>
      <!-- <el-button size="small" type="primary"></el-button> -->
    </el-upload>
    <el-dialog
      :visible.sync="show"
      title="excel"
      :close-on-click-modal="false"
      @on-cancel="cancel()"
    >
      <h4><i class="el-icon-warning"></i> </h4>
      <el-table
        :data="tableData"
        border
        style="width: 100%;"
      >
        <el-table-column
          v-for="item in columns"
          :key="item"
          :prop="item"
          :label="item"
        />
      </el-table>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="cancel">
           
        </el-button>
        <el-button
          type="primary"
          @click="confirm"
        >
           
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import XLSX from 'xlsx';
export default {
  name: 'UploadParseExcel',
  data() {
    return {
      show: false,
      columns: [],
      tableData: []
    };
  },
  created() { },
  methods: {
    confirm() {
      this.$emit('excelData', this.tableData);
      this.cancel();
    },
    cancel() {
      this.show = false;
      this.tableData = [];
    },
    httpRequest(e) {
      let file = e.file; // 
      if (!file) {
        // 
        return false;
      } else if (!/\.(xls|xlsx)$/.test(file.name.toLowerCase())) {
        // 
        this.$message.error('xlsxlsx');
        return false;
      }

      const fileReader = new FileReader();
      fileReader.onload = ev => {
        try {
          const data = ev.target.result;
          const workbook = XLSX.read(data, {
            type: 'binary' // 
          });
          const exlname = workbook.SheetNames[0]; // 
          const exl = XLSX.utils.sheet_to_json(workbook.Sheets[exlname]); // json
          this.show = true;
          if (exl.length > 0) {
            this.columns = Object.keys(exl[0]);
            this.tableData = exl;
          }
          // document.getElementsByName('file')[0].value = '' // value
        } catch (e) {
          return false;
        }
      };
      fileReader.readAsBinaryString(file);
    }
  }
};
</script>