/**
 * @name: setTimeout 1000ms
 * @param {}
 * @return:
 */
export default {
    inserted: function(el, binding) {
        el.addEventListener('click', () => {
            if (!el.disabled) {
                el.disabled = true;
                el.loading = true;
                setTimeout(() => {
                    el.disabled = false;
                }, binding.value || 3000); // 3000
            }
        });
    }
};
